import React, { useEffect } from 'react'
import code from './movieinfo.jpg'
import folder from './blogging.jpg'
import settings from './youtube.jpg'
import './Project.css'
import AOS from 'aos'
import 'aos/dist/aos.css'
import { useNavigate } from 'react-router-dom'

const Project = () => {
    useEffect(() => {
        AOS.init({ duration: 2000 })
    }, [])

    const navigate = useNavigate()
    const handleRoute = (route) => {
        navigate(`/${route}`)
    }
    return (
        <div className='w-100'>
            <div className='projects w-100 pt-5' id='projects'>
                <div className='myservicesText pt-5'>
                    <h1 className='text-uppercase text-light'>MY <span>Projects</span></h1><br />
                    <hr />
                </div>
                <div className="container text-center mt-5">
                    <div className="row">
                        {/* <div className="col-12 col-md-6 col-lg-4 p-3" data-aos="fade-right"> */}
                        <div className="col-12 col-md-6 col-lg-4 p-3" >
                            <div className="card text-dark p-3" >
                                <img src={code} className='w-100 rounded-3' style={{ border: "1px solid rgb(211,23,111)", boxShadow: "black 0px 0px 15px 1px", borderBottom: "3px solid rgb(211,23,111)" }} alt="Social Media" />
                                <div className='card-body'>
                                    <h4 className='card-title'>Movie Info App</h4>
                                    <p>This is the Movie Info App</p>
                                    <a href='' className='btn' onClick={e => handleRoute('Movie-info-app-details')}>More Information . . . </a>
                                </div>
                            </div>
                        </div>
                        <div className="col-12 col-md-6 col-lg-4 p-3">
                            {/* <div className="col-12 col-md-6 col-lg-4 p-3" data-aos="zoom-in"> */}
                            <div className="card text-dark p-3" >
                                <img src={folder} className='w-100 rounded-3' style={{ border: "1px solid rgb(211,23,111)", boxShadow: "black 0px 0px 15px 1px", borderBottom: "3px solid rgb(211,23,111)" }} alt="Social Media" />
                                <div className='card-body'>
                                    <h4 className='card-title'>Blogging App</h4>
                                    <p>This is the Working RealTime Blogging Application</p>
                                    <a href='' className='btn' onClick={e => handleRoute('Bloggin-app-details')}>More Information . . . </a>
                                </div>
                            </div>
                        </div>
                        {/* <div className="col-12 col-md-6 col-lg-4 p-3" data-aos="fade-left"> */}
                        <div className="col-12 col-md-6 col-lg-4 p-3">
                            <div className="card text-dark p-3" >
                                <img src={settings} className='w-100 rounded-3' style={{ border: "1px solid rgb(211,23,111)", boxShadow: "black 0px 0px 15px 1px", borderBottom: "3px solid rgb(211,23,111)" }} alt="Social Media" />
                                <div className='card-body'>
                                    <h4 className='card-title'>YouTube App</h4>
                                    <p>This is the clone of YouTube App</p>
                                    <a href='' className='btn' onClick={e => handleRoute('youtube-app-details')}>More Information . . . </a>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

        </div>
    )
}

export default Project